import * as React from 'react';
import './index.scss';
import bg from '../../images/weiji.jpg';

export default class HeaderBar extends React.Component<{}, {}>{
    constructor(props: {}) {
        super(props)
    };

    public render() {
        return (
            <div style={{ textAlign: 'center' }}>
                <img src={bg} />
            </div>
        )
    }
}


